.colaboradores {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.equipo {
    width: 100%;
    padding: 2rem;
    box-sizing: border-box;
    text-align: center;
    font-size: 2rem;
    position: relative;
}

.input-color { 
    position: absolute;
    top: 20px;
    right: 50px;
}
 
.equipo h3 {
    font-weight: 400;
    color: #212121;
    border-bottom:  4px solid;
    display: inline-block;
}
